.form {
  .form__text {
    margin-top: 22px;
    font-size: 13px;
    line-height: 1.85;
    text-align: left;
  }

  .form__fieldset {
    border: none;
    padding: 0;
    margin: 0;

    &--radio {
      display: flex;
      margin-top: 25px;
    }

    &--checkbox {
      display: flex;
      margin-top: 35px;
    }

    &--text {
      display: flex;
      margin-top: 16px;
    }

    &--tel {
      display: flex;
      margin-top: 19px;
    }

    &--email {
      display: flex;
      margin-top: 32px;
    }

    &--textarea {
      display: flex;
      margin-top: 33px;
    }
  }

  .form__fieldset ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .form__legend,
  .form__field {
    font-size: 17px;
    font-weight: 700;
    line-height: 0.94;
    color: #63d1bb;
    text-transform: uppercase;
    padding: 0;
    position: relative;
    margin-bottom: 20px;

    &::after {
      @include pseudo;
      width: 197px;
      height: 1px;
      background-color: #63d1bb;
      top: 7px;
      left: 62px;
    }
  }

  .form__field--tel {
    &::after {
      @include pseudo;
      width: 197px;
      height: 1px;
      background-color: #63d1bb;
      top: 11px;
      left: 62px;
    }
  }

  .form__field--email {
    &::after {
      @include pseudo;
      width: 179px;
      height: 1px;
      background-color: #63d1bb;
      top: 12px;
      left: 81px;
    }
  }
  .form__field--textarea {
    &::after {
      @include pseudo;
      width: 196px;
      height: 1px;
      background-color: #63d1bb;
      top: 12px;
      left: 63px;
    }
  }

  .form__legend li {
    padding: 0;
  }

  .form__label {
    font-size: 17px;
    font-weight: 700;
    line-height: 1.06;
    display: inline-block;
    position: relative;

    &--radio {
      padding-top: 15px;
      padding-left: 62px;
      padding-right: 20px;
      padding-bottom: 9px;

      &::before {
        @include pseudo;
        width: 36px;
        height: 36px;
        border: 2px solid #444444;
        border-radius: 50%;
        top: 7px;
        left: 2px;
      }

      &::after {
        @include pseudo;
        width: 16px;
        height: 16px;
        background-color: #62d1ba;
        border-radius: 50%;
        top: 7px;
        left: 2px;
        display: none;
      }
    }

    &--checkbox {
      padding-top: 15px;
      padding-left: 62px;
      padding-right: 20px;
      padding-bottom: 28px;

      &::before {
        @include pseudo;
        width: 38px;
        height: 38px;
        border: 2px solid #444444;
        top: 6px;
        left: 1px;
      }

      &::after {
        @include pseudo;

        width: 23px;
        height: 17px;
        background-image: url("../img/icon-tick.svg");
        background-repeat: no-repeat;
        background-size: 23px 17px;
        top: 17px;
        left: 8px;
        display: none;
      }
    }

    &--text {
      padding-top: 9px;
      padding-left: 0;
      padding-right: 20px;
      padding-bottom: 16px;

    }
  }

  .form__control {
    border-bottom: 2px solid #000000;
    border-top: none;
    border-left: none;
    border-right: none;
    width: 100%;
    color: rgba(68, 68, 68, 0.3);
    font-weight: 200;

    &--text {
      font-size: 17px;
      line-height: 1.06;
      padding-bottom: 15px;
      margin-bottom: 13px;
    }

    &--tel {
      font-size: 19px;
      line-height: 0.95;
      padding-top: 19px;
      padding-bottom: 14px;
    }

    &--email {
      font-size: 19px;
      line-height: 0.95;
      padding-top: 19px;
      padding-bottom: 14px;
    }
  }

  .form__textarea--text {
    font-size: 17px;
    line-height: 1.06;
    width: 100%;
    padding: 17px;
    border: 2px solid #444444;
    margin-top: 24px;
    min-height: 142px;
  }

  .form__notes {
    display: inline-block;
    font-size: 14px;
    line-height: 1.14;
    margin-top: 10px;
  }

  .form__btn {
    background-color: #ffffff;
    margin-top: 31px;
    padding-top: 3px;
    margin-bottom: 16px;
  }
}
