.logo {
  display: inline-block;

  .logo__title {
    fill: #000000;
  }

  .logo__background {
    &--desktop {
      display: none;
    }

    &--tablet {
      display: none;
    }
  }

  .logo__border-bottom--footer {
    display:inline-block;
  }

  @media (min-width: $tablet-width) {
    .logo__border-bottom--footer {
      display: none;
    }

    .logo__background {
      &--tablet {
        display: inline-block;
      }
    }

    .logo__title {
      fill: #ffffff;
    }
  }

  @media (min-width: $desktop-width) {
    .logo__background {
      &--tablet {
        display: none;
      }

      &--desktop {
        display: inline-block;
      }
    }
  }
}
